html {
  font-family: "Open Sans", "roboto", monospace;
}
/* 
#root {
  height: 100vmin;
  width: 100vmax;
} */

html,
body {
  width: 100vw;
  max-width: 100%;
  background-color: #f0f0c9;
}

body {
  min-height: 100vh;
  max-width: 100vw;

  overflow-x: hidden;
}

.App {
  margin: 0;
  /* height: 100%;
  height: -webkit-fill-available;
  width: 100%; */
}

.app-header {
  background-color: #042a2b;
  padding: 8px 12px;
  max-height: 83px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: #f0f0c9;
}

.site-title {
  display: flex;
  flex-direction: column;
}

.site-title h2,
.site-title h5 {
  line-height: 1em;
  margin: 0 auto;
}

.error404 {
  font-weight: 700;
  font-size: 10vh;
  height: 70vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", monospace, cursive;
}

h1 {
  margin: 10px 0;
  font-weight: 300;
}

h2 {
  margin: 10px 0;
}

h3 {
  margin: 10px;
}

h4 {
  margin: 10px 0;
  font-size: x-large;
  font-weight: 300;
}

h5 {
  margin: 10px 0;
}

ul {
  list-style-type: none;
  padding-left: 0px;
}

li {
  list-style-position: outside;
}

a {
  text-decoration: none;
}
a:link,
a:visited {
  color: inherit;
}
button {
  background-color: inherit;
  border: none;
  cursor: pointer;
}

select {
  -webkit-appearance: menulist;
  -moz-appearance: menulist;
  background-color: inherit;
  color: inherit;
  border: 0;
  padding: 2px;
}

/* ----------- MAIN NAV STYLE ----------- */
/* -------------------------------------------------------- */

.main-nav {
  display: flex;
  flex-direction: row;
  font-weight: 900;
  border-radius: 5px;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}
.main-nav li {
  margin: 4px;
  height: 54px;
  border-radius: 5px;
  background-color: #124e78;
}
.main-nav a {
  display: flex;
  padding: 6px;
  color: #bd632f;
  border-radius: 5px;
  transition: transform 200ms ease-in-out;
  will-change: transform;
}

.main-nav a:hover {
  box-shadow: #042a2b 0px 1px 3px 0px;
  color: #124e78;
  background: rgba(242, 187, 5);
  transform: scale(1.05);
}

a.active-nav-route {
  color: #124e78;
  background: rgba(242, 187, 5);
  border-color: rgba(0, 0, 0, 0.1);
}

/* ----------- WRAPPER ----------- */
/* -------------------------------------------------------- */

.body-wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 1px;
  margin: 0 auto;
}

.wrapper {
  position: relative;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  margin: 0 auto;
}

/* ----------- NAV OPTIONS ----------- */
/* -------------------------------------------------------- */

.nav-options {
  position: -webkit-sticky;
  position: -moz-sticky;
  position: -ms-sticky;
  position: -o-sticky;
  position: sticky;
  top: 0;
  z-index: 12;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  height: 50px;
  margin: 0 10px;
  background-color: #f0f0c9fa;
  border-bottom: solid 1px #114a74;
}

.nav-options select {
  margin-left: 15px;
  margin-right: 15px;
  color: #bd632f;
  background-color: inherit;
  border-style: none;
  box-shadow: 0px 0px 2px 0px inset #124e78;
}

.nav-options button {
  margin: 0 15px;
}

.nav-page {
  background-color: inherit;
  border-style: none;
  margin-left: 15px;
  margin-right: 15px;
  transition: all 200ms ease-in-out;
}

.items-size-button {
  display: flex;
  align-items: center;
  cursor: pointer;
  height: 40px;
  margin: 0 15px;
}

.grid-icon {
  margin: 3px;
  fill: #124e78;
  padding: 3px;
  border-radius: 2px;
  border: solid 2px #124e7800;
}

.grid-icon-selected {
  border: solid 2px #124e78;
  box-shadow: #124e78 inset 0 0 1px;
}

.diceroll {
  transition: transform 2.5s ease-in-out;
}
.diceroll:hover {
  transform: rotate(720deg);
  transition: transform 4s ease-in;
}

/* MODAL ROOT */

#modal-root {
  z-index: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

/* ----------- FILTERS & SLIDERS ----------- */
/* -------------------------------------------------------- */

.nav-filter-title {
  /* border-bottom: 1px solid #bd632f; */
