.filter {
  margin: 10px 5px;
  justify-content: flex-start;
  text-align: center;
  /* transition: opacity 1s ease-in-out; */
}

.filter ul {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.option-title {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  z-index: 2;
  cursor: pointer;
  background-color: #f0f0c9;
  opacity: 1;
  border: 1px solid #bd632f;
  border-width: 0 0 1px 0;
}

.option-title h3 {
  text-align: start;
  font: 700 14px/50px;
  text-transform: uppercase;
}

.option-title h3::before {
  content: "";
  width: 8px;
  height: 8px;
  border: 2px solid #1d1d1b;
  border-width: 0 1px 1px 0;
  transform: rotate(-45deg);
  position: absolute;
  margin: 5px auto;
  right: 10%;
  box-sizing: border-box;
  transition: all 150ms ease-in-out;
}

.opened h3::before {
  transform: rotate(45deg);
}

.filter-option-list {
  display: flex;
  justify-content: flex-start;
  margin: 0 auto;
  border-width: 0 0 0 0;
  border-style: solid;
  border-color: #bd632f;
}

.filter-option-list li:first-child {
  border-width: 0 0 1px 0;
}
.filter-option-list li:last-child {
  margin-bottom: 30px;
  border-radius: 10px;
}

.filter-option {
  padding: 5px;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: #bd632f94;
  margin-bottom: -1px;
  -webkit-animation: growDown 150ms ease-in-out forwards;
          animation: growDown 150ms ease-in-out forwards;
  will-change: transform;
}

.filter-option-hidden {
  padding: 0;
  margin: 0;
  height: 0;
  opacity: 0;
  position: top;
  transform-origin: top center;
}

@-webkit-keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}

@keyframes growDown {
  0% {
    transform: scaleY(0);
  }
  80% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}

.filter-option input,
.filter-option label {
  display: inline-block;
  margin: 6px;
}
.filter-option label {
  font-size: 0.9em;
  flex-grow: 1;
  text-align: start;
}
