/* ----------- MAIN BEER LIST ----------- */
/* -------------------------------------------------------- */
/* -------------------------------------------------------- */

.beer-list {
  z-index: 1;
  position: relative;
  /* overflow: scroll; */
  min-height: calc(100vh -130px);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(118px, 1fr));
  margin: 8px;
}

.beer-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  overflow: hidden;
  border-radius: 3px 5px 5px 5px;
  text-align: center;
  box-shadow: -1px -1px 5px #00000038, 0px 3px 2px #00000038;
  background-color: #ffffff34;
  margin: 6px;
  height: 264px;
  transition: border-radius 300ms ease-in-out;
}

.beer-item:hover {
  border-radius: 8px;
  transform: scale(1.02);
}

.beer-image {
  height: 200px;
  width: 76px;
  margin: auto;
  margin-top: 8px;
  object-fit: contain;
}

.pic-name {
  margin: 8px auto;
  width: 110px;
  max-height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 2;
  color: #042a2b;
}

.pic-name p {
  margin: 0;
  padding: 4px;
  height: 40px;
  display: inline-block;
  word-wrap: break-word;
  overflow: hidden;
  overflow-wrap: break-word;
  white-space: wrap;
  text-overflow: ellipsis;
}

.small-item-id {
  background-color: #124e78;
  color: #f2bb05;
  font-weight: 700;
  border-radius: 0;
}

.colorChip {
  position: absolute;
  margin: 0;
  width: 0;
  height: 0;
}

.beer-list-empty-fill {
  display: flex;
  margin: 0;
  width: 122px;
  height: 0px;
}

/* ----------- SINGLE BEER CARD ----------- */
/* -------------------------------------------------------- */

.beer-card {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;
  /* height: 100%; */
  box-shadow: -2px -2px 6px #00000038, 0px 3px 2px #00000038;
  background-color: #ffffff34;
  margin: 20px;
  padding: 30px;
  border-radius: 12px;
}

/* ----------- SINGLE BEER IMAGE ----------- */
/* -------------------------------------------------------- */

.beer-img-star {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* margin: 30px; */
}

.beer-card-image {
  margin: 0 0 30px 0;
  object-fit: contain;
  height: 45vh;
  max-height: 325px;
}

/* ----------- SINGLE BEER MAIN INFO ----------- */
/* -------------------------------------------------------- */

.beer-card-info {
  margin: 0 30px;
  min-width: 350px;
  max-width: 500px;
  flex-grow: 2;
  /* overflow: hidden; */
  display: flex;
  flex-direction: column;
  align-items: stretch;
  /* flex-grow: 1; */
  text-align: start;
}

.beer-card-info h1,
.icon-title-main {
  margin: 20px 0 0 0;
}

.beer-card-info ul,
.beer-card-info p {
  margin: 12px 0 16px 0px;
}

.beer-card-info ul {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.beer-card-info li {
  margin-bottom: 0.5em;
}

.beer-card-info li::marker,
.side-card li::marker {
  content: "— ";
  font-weight: bold;
  color: #bd632f27;
}

.icon-title-main {
  display: flex;
  align-items: center;
}

.icon-title-main img {
  margin-right: 20px;
}

.beer-description {
  padding: 0;
  margin: 0;
  max-width: 600px;
}

/* ----------- SINGLE BEER Visual DATA ----------- */
/* -------------------------------------------------------- */

.beer-card-data {
  font-size: small;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.beer-card-data div {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 3px 0;
}

.beer-card-data div span {
  display: inline-block;
  margin-right: 10px;
}

.beer-color-display {
  display: inline-block;
  border-radius: 10px;
  border: 2px solid #bd632f36;
  width: 40px;
  height: 40px;
  text-align: center;
}

/* ----------- SINGLE BEER SIDE INFO ----------- */
/* -------------------------------------------------------- */

.side-card {
  position: relative;
  justify-content: start;
  text-align: start;
  border: solid 3px #bd632f36;
  background-color: #bd632f54;
  padding: 20px;
  border-radius: 8px;
  min-width: 200px;
}

.side-card ul,
.side-card p {
  -webkit-margin-before: 0em;
          margin-block-start: 0em;
  padding-left: 40px;
  font-size: smaller;
  max-width: 34ch;
}

.icon-title-side {
  display: flex;
  align-items: center;
  /* margin-block-start: 1em; */
}

/* ----------- SINGLE BEER STAR RATING ----------- */
/* -------------------------------------------------------- */

.beer-stars {
  display: flex;
  flex-direction: row;
  justify-content: center;
  object-fit: contain;
}

.beer-stars li:first-child {
  padding-left: 0;
}
.beer-stars li {
  object-fit: contain;
  list-style: none;
  text-align: center;
  padding: 3px;
  box-sizing: border-box;
}

/* .beer-stars li:first-child {
  padding-left: 0;
} */

.beer-stars li:hover {
  cursor: pointer;
}

.beer-stars .star {
  height: auto;
  fill: #bd632f36;
  object-fit: contain;
  transition: transform 200ms ease-in-out;
  will-change: transform, animation;
}

.star:hover {
  transform: scale(1.2);
}

.beer-stars .selected .star {
  fill: #f2bb05;
  -webkit-animation: grow 0.33s ease-in-out;
          animation: grow 0.33s ease-in-out;
  will-change: transform;
}

@-webkit-keyframes grow {
  30% {
    transform: scale(1.2) rotate(-3deg);
  }
  30% {
    transform: scale(0.95) rotate(-3deg);
  }
  70% {
    transform: scale(1.1) rotate(+3deg);
  }
}

@keyframes grow {
  30% {
    transform: scale(1.2) rotate(-3deg);
  }
  30% {
    transform: scale(0.95) rotate(-3deg);
  }
  70% {
    transform: scale(1.1) rotate(+3deg);
  }
}

/* ----------- TOP BEERS lIST & MINI BEER CARDS ----------- */
/* -------------------------------------------------------- */

.top-beers-list {
  z-index: 1;
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 8px;
}

.mini-beer-card {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  box-shadow: -1px -1px 5px #00000038, 0px 3px 2px #00000038;
  background-color: #ffffff34;
  height: 200px;
  margin: 6px;
  border-radius: 3px 8px 8px 8px;
  overflow: hidden;
  transition: border-radius 300ms ease-in-out;
}

.mini-beer-card:hover {
  border-radius: 8px;
  transform: scale(1.02);
}
.mini-beer-card a {
  padding: 0;
  margin: 0;
  object-fit: contain;
}
.mini-beer-card img {
  padding: 12px;
  margin: 0;
  height: 160px;
}

.mini-card-info {
  padding: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  flex-grow: 1;
  text-align: start;
  overflow-wrap: break-word;
  line-break: loose;
  word-break: break-word;
  text-overflow: ellipsis;
  overflow: hidden;
}

.mini-beer-card p {
  overflow-wrap: break-word;
  line-break: loose;
  word-break: break-word;
  text-overflow: ellipsis;
  padding: 12px 4px;
  margin: 0;
}

.mini-beer-card .mini-card-title {
  font-weight: 500;
  font-size: 1em;
}

.mini-beer-stars {
  padding: 12px;
  margin: 0px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  object-fit: contain;
  align-self: flex-end;
}

/* MEDIA QUERIES */

/* @media (max-width: 700px) {
